import React from "react";
import axios from "axios";
import Movie from "./Movie";
import "./App.css";

class App extends React.Component {
  state = {
    isLoading: true,
    movies: []
  };
  getMovies = async () => { //뭐좀기다려
    const {
      data: {
        data: { movies }
      }
    } = await axios.get("https://yts.mx/api/v2/list_movies.json?sort_by=rating"); //나좀 기다려
    this.setState({ movies : movies, isLoading: false }) //movies : movies 근데 하나만 써도 똑같대
  }
  async componentDidMount() { //여기서 data fetch할꺼임  async didmount 끝날때까지 시간 좀 걸린다고 알려주는거
    this.getMovies();
  }
  render() {
    const { isLoading, movies } = this.state;
    return (
      <section className ="container">
        {isLoading ? (
          <div className="loader">
            <span className="loader__text">Loading...</span>
          </div>
        ) : (
            <div className="movies">
              {movies.map(movie => (
                <Movie
                  key={movie.id}
                  id={movie.id}
                  year={movie.year}
                  title={movie.title}
                  summary={movie.summary}
                  poster={movie.medium_cover_image} 
                  gneres={movie.genres}
                  />
              ))}
            </div>
          )}

      </section>

    );
  }
}
export default App;